"use client"
import { useState, useEffect } from 'react'
import {
  List,
  BarChart3,
  Plus,
  Search,
  Eye,
  Edit,
  Trash2,
  MapPin,
  DollarSign,
  Calendar,
  Users,
  FileText,
  Phone,
  Video,
  UserCheck,
  Mail,
  Download,
  X,
  CheckCircle,
  XCircle,
  Loader2
} from 'lucide-react'

const JobManagement = () => {
  const [currentView, setCurrentView] = useState('list')
  const [showJobDetail, setShowJobDetail] = useState(false)
  const [showInterviewsList, setShowInterviewsList] = useState(false)
  const [showDeleteConfirm, setShowDeleteConfirm] = useState(false)
  const [activeTab, setActiveTab] = useState('applications')

  const [jobs, setJobs] = useState([])
  const [jobApplications, setJobApplications] = useState([])
  const [scheduledInterviews, setScheduledInterviews] = useState([])
  const [applicationCounts, setApplicationCounts] = useState({})
  const [selectedJob, setSelectedJob] = useState(null)
  const [jobToDelete, setJobToDelete] = useState(null)
  const [applicationToDelete, setApplicationToDelete] = useState(null)
  const [deleteType, setDeleteType] = useState('job')

  const [loading, setLoading] = useState(false)
  const [loadingApplications, setLoadingApplications] = useState(false)
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [isDeleting, setIsDeleting] = useState(false)

  const [searchQuery, setSearchQuery] = useState('')
  const [statusFilter, setStatusFilter] = useState('')
  const [selectedStatusFilter, setSelectedStatusFilter] = useState('')

  const [jobForm, setJobForm] = useState({
    id: null,
    title: '',
    email: '',
    employer: '',
    location: '',
    salary: '',
    jobType: '',
    experience_level: '',
    skills: '',
    job_description: '',
    deadline: '',
    status: 'active'
  })

  const applicationStatusOptions = [
    { title: 'Submitted', value: 'submitted' },
    { title: 'Under Review', value: 'under_review' },
    { title: 'Interview Scheduled', value: 'interview_scheduled' },
    { title: 'Interviewed', value: 'interviewed' },
    { title: 'Offered', value: 'offered' },
    { title: 'Rejected', value: 'rejected' },
    { title: 'Withdrawn', value: 'withdrawn' }
  ]

  const fetchJobs = async () => {
    setLoading(true)
    try {
      await new Promise(r => setTimeout(r, 500))
      const mockJobs = [
        { _id: '1', title: 'Senior Software Engineer', employer: 'Qwesi Tech', location: 'Remote', salary: '$80k-$120k', job_description: 'Experienced engineer', status: 'active', createdAt: new Date().toISOString(), views: 150 },
        { _id: '2', title: 'Product Manager', employer: 'Qwesi Solutions', location: 'New York, NY', salary: '$90k-$130k', job_description: 'Lead product initiatives', status: 'paused', createdAt: new Date().toISOString(), views: 85 }
      ]
      setJobs(mockJobs)
      setApplicationCounts({ '1': 12, '2': 8 })
    } catch (err) {
      console.error(err)
    } finally { setLoading(false) }
  }

  const fetchJobApplications = async (jobId) => {
    setLoadingApplications(true)
    try {
      await new Promise(r => setTimeout(r, 300))
      setJobApplications([
        { _id: 'app1', applicantDetails: { firstName: 'John', lastName: 'Doe', email: 'john@example.com' }, status: 'submitted', submittedAt: new Date().toISOString(), resume: { url: '/resume1.pdf', originalName: 'John_Doe_Resume.pdf' } },
        { _id: 'app2', applicantDetails: { firstName: 'Jane', lastName: 'Smith', email: 'jane@example.com' }, status: 'under_review', submittedAt: new Date().toISOString(), resume: { url: '/resume2.pdf', originalName: 'Jane_Smith_Resume.pdf' } }
      ])
    } catch (err) {
      console.error(err)
      setJobApplications([])
    } finally { setLoadingApplications(false) }
  }

  const fetchScheduledInterviews = async () => {
    await new Promise(r => setTimeout(r, 200))
    setScheduledInterviews([
      { _id: 'int1', candidateName: 'John Doe', candidateEmail: 'john@example.com', status: 'scheduled', date: '2024-12-15', startTime: '14:00', duration: 60, interviewType: 'video', meetingLink: 'https://meet.google.com/abc-defg-hij' }
    ])
  }

  useEffect(() => { fetchJobs(); fetchScheduledInterviews() }, [])

  const filteredJobs = jobs.filter(job => {
    const matchesSearch = !searchQuery || job.title.toLowerCase().includes(searchQuery.toLowerCase()) || job.employer.toLowerCase().includes(searchQuery.toLowerCase()) || job.location.toLowerCase().includes(searchQuery.toLowerCase())
    const matchesStatus = !statusFilter || job.status === statusFilter
    return matchesSearch && matchesStatus
  })

  const activeJobs = jobs.filter(j => j.status === 'active').length
  const totalApplications = Object.values(applicationCounts).reduce((s, c) => s + (c || 0), 0)
  const totalViews = jobs.reduce((s, j) => s + (j.views || 0), 0)

  const resetJobForm = () => setJobForm({ id: null, title: '', email: '', employer: '', location: '', salary: '', jobType: '', experience_level: '', skills: '', job_description: '', deadline: '', status: 'active' })

  const submitJobForm = async (e) => {
    e.preventDefault(); setIsSubmitting(true)
    try {
      await new Promise(r => setTimeout(r, 600))
      alert(`Job ${currentView === 'create' ? 'created' : 'updated'} successfully`)
      resetJobForm(); setCurrentView('list'); fetchJobs()
    } catch (err) { console.error(err); alert('Submit failed') } finally { setIsSubmitting(false) }
  }

  const handleInputChange = (name, value) => setJobForm(prev => ({ ...prev, [name]: value }))

  const viewJob = async (jobId) => { const job = jobs.find(j => j._id === jobId); setSelectedJob(job); setShowJobDetail(true); await fetchJobApplications(jobId) }

  const editJob = (job) => { setCurrentView('edit'); setJobForm({ ...job }) }
  const deleteJob = (jobId) => { setJobToDelete(jobId); setDeleteType('job'); setShowDeleteConfirm(true) }
  const deleteApplication = (appId) => { setApplicationToDelete(appId); setDeleteType('application'); setShowDeleteConfirm(true) }

  const confirmDelete = async () => {
    setIsDeleting(true)
    try {
      await new Promise(r => setTimeout(r, 400))
      if (deleteType === 'job') setJobs(prev => prev.filter(j => j._id !== jobToDelete))
      else setJobApplications(prev => prev.filter(a => a._id !== applicationToDelete))
      alert('Deleted')
    } catch (err) { console.error(err); alert('Delete failed') } finally { setIsDeleting(false); setShowDeleteConfirm(false); setJobToDelete(null); setApplicationToDelete(null) }
  }

  const formatDate = (d) => d ? new Date(d).toLocaleDateString() : 'N/A'
  const truncate = (t, n=120) => t ? (t.length > n ? t.slice(0,n)+'...' : t) : ''

  return (
    <div className="min-h-screen bg-slate-50">
      <PageHeader title="Job Management" subtitle="Manage job postings and review applications">
        <button onClick={() => setShowInterviewsList(true)} className="px-3 py-2 bg-blue-100 rounded"> <Calendar className="w-4 h-4"/> <span className="ml-2 hidden sm:inline">Interviews</span></button>
        {currentView === 'list' ? <button onClick={() => setCurrentView('create')} className="px-3 py-2 bg-orange-600 text-white rounded"> <Plus className="w-4 h-4"/> <span className="ml-2 hidden sm:inline">Create Job</span></button> : <button onClick={() => setCurrentView('list')} className="px-3 py-2 bg-slate-100 rounded">Back</button>}
      </PageHeader>

      <main className="p-6 max-w-6xl mx-auto">
        {currentView === 'list' && (
          <>
            <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-6">
              <div className="bg-white p-4 rounded border"><div className="text-sm text-slate-500">Total Jobs</div><div className="text-2xl font-bold">{jobs.length}</div></div>
              <div className="bg-white p-4 rounded border"><div className="text-sm text-slate-500">Active Jobs</div><div className="text-2xl font-bold">{activeJobs}</div></div>
              <div className="bg-white p-4 rounded border"><div className="text-sm text-slate-500">Applications</div><div className="text-2xl font-bold">{totalApplications}</div></div>
              <div className="bg-white p-4 rounded border"><div className="text-sm text-slate-500">Total Views</div><div className="text-2xl font-bold">{totalViews}</div></div>
            </div>

            <div className="bg-white p-4 rounded mb-6 flex gap-4">
              <div className="relative flex-1">
                <Search className="absolute left-3 top-3 w-4 h-4 text-slate-400" />
                <input className="pl-10 pr-3 py-2 w-full border rounded" placeholder="Search jobs..." value={searchQuery} onChange={e=>setSearchQuery(e.target.value)} />
              </div>
              <select className="px-3 py-2 border rounded" value={statusFilter} onChange={e=>setStatusFilter(e.target.value)}>
                <option value="">All Status</option>
                <option value="active">Active</option>
                <option value="paused">Paused</option>
                <option value="closed">Closed</option>
              </select>
            </div>

            {loading ? <div className="text-center py-12"><Loader2 className="w-8 h-8 mx-auto" /><div>Loading...</div></div> : (
              filteredJobs.length === 0 ? (
                <div className="bg-white p-12 rounded text-center">No jobs found</div>
              ) : (
                <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                  {filteredJobs.map(job => (
                    <div key={job._id} className="bg-white p-6 rounded border cursor-pointer" onClick={()=>viewJob(job._id)}>
                      <div className="flex justify-between items-start">
                        <div>
                          <h3 className="font-bold text-lg">{job.title}</h3>
                          <div className="text-sm text-slate-600">{job.employer}</div>
                        </div>
                        <div className={`px-3 py-1 rounded-full text-xs ${job.status==='active' ? 'bg-green-100 text-green-700' : job.status==='paused' ? 'bg-orange-100 text-orange-700' : 'bg-red-100 text-red-700'}`}>{job.status}</div>
                      </div>
                      <div className="mt-3 text-sm text-slate-700">{truncate(job.job_description,120)}</div>
                      <div className="mt-4 flex gap-2">
                        <button onClick={(e)=>{e.stopPropagation(); editJob(job)}} className="px-3 py-2 bg-orange-100 rounded text-orange-700">Edit</button>
                        <button onClick={(e)=>{e.stopPropagation(); deleteJob(job._id)}} className="px-3 py-2 bg-red-100 rounded text-red-700">Delete</button>
                      </div>
                    </div>
                  ))}
                </div>
              )
            )}
          </>
        )}

        {(currentView === 'create' || currentView === 'edit') && (
          <div className="bg-white p-6 rounded border">
            <form onSubmit={submitJobForm} className="space-y-4">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label className="text-sm">Job Title *</label>
                  <input required value={jobForm.title} onChange={e=>handleInputChange('title', e.target.value)} className="w-full border px-3 py-2 rounded" />
                </div>
                <div>
                  <label className="text-sm">Company Name *</label>
                  <input required value={jobForm.employer} onChange={e=>handleInputChange('employer', e.target.value)} className="w-full border px-3 py-2 rounded" />
                </div>
                <div>
                  <label className="text-sm">Email *</label>
                  <input required type="email" value={jobForm.email} onChange={e=>handleInputChange('email', e.target.value)} className="w-full border px-3 py-2 rounded" />
                </div>
                <div>
                  <label className="text-sm">Location</label>
                  <input value={jobForm.location} onChange={e=>handleInputChange('location', e.target.value)} className="w-full border px-3 py-2 rounded" />
                </div>
              </div>

              <div>
                <label className="text-sm">Job Description</label>
                <textarea value={jobForm.job_description} onChange={e=>handleInputChange('job_description', e.target.value)} className="w-full border px-3 py-2 rounded" rows={6} />
              </div>

              <div className="flex justify-end">
                <button type="submit" disabled={isSubmitting} className="px-4 py-2 bg-blue-600 text-white rounded">{isSubmitting ? 'Saving...' : 'Save Job'}</button>
              </div>
            </form>
          </div>
        )}

        {showJobDetail && selectedJob && (
          <div className="fixed inset-0 bg-black/40 flex items-center justify-center z-50">
            <div className="bg-white rounded p-6 w-full max-w-3xl">
              <div className="flex justify-between items-start">
                <div>
                  <h2 className="text-xl font-bold">{selectedJob.title}</h2>
                  <div className="text-sm text-slate-600">{selectedJob.employer} • {selectedJob.location}</div>
                </div>
                <button onClick={()=>setShowJobDetail(false)} className="text-slate-500"><X className="w-5 h-5"/></button>
              </div>
              <div className="mt-4 text-sm text-slate-700">{selectedJob.job_description}</div>
              <div className="mt-4 flex gap-2">
                <button onClick={()=>{ setCurrentView('edit'); setJobForm({...selectedJob}); setShowJobDetail(false)}} className="px-3 py-2 bg-orange-100 rounded">Edit</button>
                <button onClick={()=>{ deleteJob(selectedJob._id); setShowJobDetail(false)}} className="px-3 py-2 bg-red-100 rounded">Delete</button>
              </div>
            </div>
          </div>
        )}

        {showInterviewsList && (
          <div className="fixed inset-0 bg-black/40 z-40 flex items-end md:items-center justify-center">
            <div className="bg-white rounded-t-lg md:rounded-lg w-full md:max-w-3xl p-6">
              <div className="flex justify-between items-center">
                <h3 className="font-bold">Scheduled Interviews</h3>
                <button onClick={()=>setShowInterviewsList(false)}><X className="w-5 h-5"/></button>
              </div>
              <div className="mt-4">
                {scheduledInterviews.length === 0 ? <div className="text-center py-8">No interviews scheduled</div> : (
                  scheduledInterviews.map(i => (
                    <div key={i._id} className="border rounded p-3 mb-3">
                      <div className="flex justify-between items-center">
                        <div>
                          <div className="font-semibold">{i.candidateName}</div>
                          <div className="text-sm text-slate-600">{i.interviewType} • {formatDate(i.date)} {i.startTime}</div>
                        </div>
                        <div className="flex gap-2">
                          <button onClick={()=>window.open(i.meetingLink,'_blank')} className="px-3 py-2 bg-blue-100 rounded">Join</button>
                        </div>
                      </div>
                    </div>
                  ))
                )}
              </div>
            </div>
          </div>
        )}

        {showDeleteConfirm && (
          <div className="fixed inset-0 bg-black/30 flex items-center justify-center z-50">
            <div className="bg-white p-6 rounded">
              <h4 className="font-bold mb-2">Confirm Delete</h4>
              <p className="text-sm text-slate-600 mb-4">Are you sure you want to delete this {deleteType}?</p>
              <div className="flex justify-end gap-2">
                <button onClick={()=>{ setShowDeleteConfirm(false) }} className="px-3 py-2 border rounded">Cancel</button>
                <button onClick={confirmDelete} className="px-3 py-2 bg-red-600 text-white rounded">{isDeleting ? 'Deleting...' : 'Delete'}</button>
              </div>
            </div>
          </div>
        )}

      </main>
    </div>
  )
}

export default JobManagement